import React from 'react';
import Works from './elements/Works';

const works  = require('./../../assets/data/works.json');

const BriefcaseMain = () => {

    return (
        <main className="BriefcaseMain">

            {
                works.map((item) => {
                    return (
                        <Works key={item.id}
                            title={item.title}
                            subtitle={item.subtitle}
                            tecnologies={item.tecnologies}
                            url={item.url}
                            status={item.status}
                            imageUrl={item.image}
                        ></Works>
                    )
                })
            }

        </main>
    );
};

export default BriefcaseMain;