import React from 'react';

const KnowledgeItem = ({title, image, delay}) => {

    const loadImage = image => (require(`./../../../assets/images/logos/${image}`).default);

    return (
        <article className={`knowledge__list__item wow animate__animated animate__zoomInDown animate__delay-${delay}s`} id={title}>
            <figure className="knowledge__list__item__picture">
                        {<img src={loadImage(image)} alt={title}/>}
            </figure>
            <h3 className="knowledge__list__item__title">{title}</h3>
        </article>
    );
};

export default KnowledgeItem;