import React from 'react';
import KnowledgeItem from './elements/KnowledgeItem';
const dataKnowledge = require('../../assets/data/knowledge.json');

const Knowledge = () => {

    return (
        <section className="knowledge">
            <h2 className="sectionTitle knowledge__title">Conocimientos</h2>

            <div className="knowledge__list">
            {
                dataKnowledge.map((item) => {
                    return (
                        <KnowledgeItem key={item.id}
                            title={item.title}
                            image={item.image}
                        />
                    )
                })
            }

            </div>
            
        </section>
    );
};

export default Knowledge;
